body {
    color: #201518;
    background-color: #D1CCC5;
    font-size: 1rem;
    margin: 0;
    padding: 0;
    min-height: 100vh;
}

a {
    text-decoration: none;
}

hr {
    border-top: #201518 solid .5px;
    border-bottom: #201518 solid .5px;
    border-left: none;
    border-right: none;
    height: 3px;
    margin: 1.5em 0;
}

/**** utilities ****/
.no-ul {
    list-style-type: none;
    display: flex;
}

.no-ul > li {
    padding: 0 .5em;
}

.row {
    display: flex;
}

.col {
    flex: 50%;
}

/**** main ****/
#main {
    max-width: 75%;
    margin: auto;
    padding: 1.5em;
}

.title {
    font-size: 3em;
    margin: 0;
}

.subtitle {
    font-style: italic;
    font-size: 1.2em;
    margin: 0;
}

/**** character pages ****/
.image-row {
    display: flex;
    flex-wrap: wrap;
}

.chr {
    width: 12em;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1.5em;
}

.chr-img {
    height: 175px;
    width: 150px;
    object-fit: cover;
    border: #201518 solid 1px;
    margin: 0 1.5em;
}

.chr-name {
    text-align: center;
    margin: 0;
}

.chr-link {
    margin: .5em;
}

.chr-summary {
    margin: 0;
    text-align: justify;
    max-height: 5em;
    overflow: auto;
    scrollbar-width: thin;
}

.chr-row {
    display: flex;
}

.chr-row > p {
    margin: 0;
    padding: 0 .5em 0 0;
}

/**** infobox ****/
.infobox {
    width: 25em;
    border: #201518 solid 1px;
}

.infobox-img {
    width: 100%;
    height: 20em;
    object-fit: cover;
}

.infobox-info {
    padding: 1.5em;
}

/**** tabs ****/
 /* Style the tab */
 .tab {
    overflow: hidden;
    border: 1px solid #ccc;
    background-color: #f1f1f1;
  }
  
  /* Style the buttons that are used to open the tab content */
  .tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
  }
  
  /* Change background color of buttons on hover */
  .tab button:hover {
    background-color: #ddd;
  }
  
  /* Create an active/current tablink class */
  .tab button.active {
    background-color: #ccc;
  }
  
  /* Style the tab content */
  .tabcontent {
    display: none;
  } 

